html, *, :before, :after {
  box-sizing: border-box;
}

.container {
  min-width: 320px;
  max-width: 414px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 930px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (main-width: 1280px) {
  .container {
    max-width: 1110px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

:root {
  --modal-backdrop: #2e2f42;
  --modal-background: #fcfcfc;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
  display: block;
}

.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  min-height: 668px;
  background-color: #fffdee;
  padding: 60px 15px;
  position: absolute;
  top: 0;
  left: 0;
}

.close-button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-66%, 66%);
}

.icon-cross {
  stroke: #000;
}

.booking-offer {
  margin-left: auto;
  margin-right: auto;
}

.booking-table {
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 20px;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
}

.booking-table > .wrapper {
  width: 100%;
}

.area-name {
  letter-spacing: .02em;
  color: #c79002;
  padding-left: 12px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

.booking-table .input {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #c79002;
  border-radius: 2px;
  margin-bottom: 8px;
  display: block;
}

.button-booking-table {
  color: #fff;
  width: 100%;
  background: #000;
  border: 1px solid #f3c653;
  border-radius: 2px;
  padding: 20px 43px;
}

